@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.body{
    /* margin: 0; */
    /* padding: 0; */
    box-sizing: border-box;
    font-family: 'Popins', sans-serif;
}

body{
    background: #081b29;
    color: #ededed;
}

.containerMain{
    height: 100vh;
    display: flex;
    align-items: center;
    padding: 0 8%;
}

.main{
    max-width: 500px;
}

.main h1{
    font-size: 40px;
    font-weight: 700;
    line-height: 1.2;
}

.main h3{
    font-size: 22px;
    font-weight: 500;
    color: rgb(255, 255, 71);
}

.main p{
    font-size: 16px;
    margin: 20px 0px 40px;
}

.main .btn-box{
    display: flex;
    justify-content: space-between;
    width: 340px;
    height: 50px;
    
}
.btn-box a{
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 100%;
    background: rgb(255, 255, 71);
    border: 2px solid rgb(255, 255, 71);
    border-radius: 8px;
    font-size: 19px;
    color: #081b29;
    text-decoration: none;
    font-weight: 600;
    letter-spacing: 1px;
    z-index: 1;
    overflow: hidden;
    transition: .5s;
}

.btn-box a:hover{
    color: rgb(255, 255, 71);
}

.btn-box a:nth-child(2){
    background: transparent;
    color: rgb(255, 255, 71);
}

.btn-box a:nth-child(2):hover{
    color: #081b29;
}

.btn-box a:nth-child(2)::before{
    background: rgb(255, 255, 71);
}

.btn-box a::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #081b29;
    z-index: -1;
    transition: .5s ;
}

.btn-box a:hover::before{
    width: 100%;
}

