@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
    scroll-behavior: smooth;

}
.body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Popins', sans-serif;
}

body{
    background: #081b29;
    color: #ededed;
}

.container{
    position: fixed;
    top: 0;
    left: 15px;
    width: 95%;
    padding: 20px 10px;
    background: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* z-index: 100; */
}

.al{
    font-size: 25px;
    color: white;
    text-decoration: none;
    font-weight: 600;
}

.navbar a{
    font-size: 18px;
    color: #ededed;
    text-decoration: none;
    font-weight: 500;
    margin-left: 35px;
    transition: 9s;
    position: relative;
}

.nama span{
    color: rgb(255, 255, 71);
}

.navbar a::after{
    content: '';
    height: 3px;
    width: 0;
    background: rgb(255, 255, 71);
    position: absolute;
    left: 0;
    bottom: -5px;
    transition: 0.5s;
}

.navbar a:hover::after{
    width: 100%;
}

 
.navbar a.active{
    color: rgb(255, 255, 71);
}


